.footer {
	background-color: $secondaryColor;
	color: $primaryColor;
	font-size: 14px;
	&__logo {
		margin-bottom: 30px;
		max-width: 180px;
		padding-top: 50px;
	}
	&__top {
		display: flex;
		flex-direction: column;
		@media screen and (min-width: 768px) {
			flex-direction: row;
		}
	}
	&__col {
		margin-bottom: 30px;
		@media screen and (min-width: 768px) {
			margin-bottom: 0;
		}
		ul {
			a {
				line-height: 1.43;
			}
		}
	}
	&__bottom {
		background-color: $primaryColor;
		color: #333333;
		padding-top: 7px;
		padding-bottom: 7px;
		margin-top: 23px;
		font-size: 13px;
		line-height: 30px;
	}
}
